@import "@/assets/global.scss";






























































































































.tab {
  background: #FFFFFF;
  padding: 13px 16px;
  display: flex;
  position: relative;
  border-bottom: 1px solid #FAFAFA;

  > .option {
    background: #FFFFFF;
    border: 1px solid #BDBDBD;
    box-sizing: border-box;
    border-radius: 24px;
    padding: 8px 12px 8px 18px;
    margin-right: 12px;
    display: flex;
    @extend .font-16-normal;
    @extend .font-color-6;

    > span {
      margin-right: 8px;
    }
  }

  > .selected {
    @extend .font-color-hint;
    border: 1px solid #FCA311;
  }
}

.list {
  position: absolute;
  top: 69px;
  left: 0;
  background: #FFFFFF;
  width: 100%;
  max-height: 262px;
  overflow-y: auto;

  > div {
    @extend .font-14-normal;
    @extend .font-color-8;
    text-align: left;
    padding: 10px 24px;
  }
}

.fadeHeight-enter-active,
.fadeHeight-leave-active {
  transition: all 0.2s;
  max-height: 262px;
}
.fadeHeight-enter,
.fadeHeight-leave-to
{
  max-height: 0;
}
