@import "@/assets/global.scss";






































.item-panel {
  width: 300px;
  max-height: 133px;
  padding: 18px 12px 18px 14px;
  background: #FFFFFF;
  box-sizing: border-box;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  display: flex;
}

.txtContent {
  width: 176px;
  margin-left: 8px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
